	module.exports = {"data":{"markdownRemark":{"html":"<p>You can customize your program's response SLA times by configuring the number of business days. To customize your program's response SLA time: </p>\n<ol>\n<li>Go to <strong>Settings > Program > Response SLA</strong>. </li>\n<li>Input the target response SLA time in days for your program under the <strong>Target</strong> column. </li>\n</ol>\n<p>Keep in mind that:</p>\n<ul>\n<li>\n<p>It's recommended that you're targets fall within the Recommended and Limit times. </p>\n</li>\n</ul>\n<table>\n<thead>\n<tr>\n<th>Time</th>\n<th>Recommended</th>\n<th>Limit</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Time to first response</td>\n<td>1 day</td>\n<td>5 days</td>\n</tr>\n<tr>\n<td>Time to triage</td>\n<td>2 days</td>\n<td>10 days</td>\n</tr>\n<tr>\n<td>Time to bounty</td>\n<td>1 day (after triage)</td>\n<td>N/A</td>\n</tr>\n<tr>\n<td>Time to resolution</td>\n<td>30 days</td>\n<td>N/A</td>\n</tr>\n</tbody>\n</table>\n<ul>\n<li>To edit your program's response SLA settings, you need to have program permissions. </li>\n<li>Modified targets will be applied to reports created after the modification. </li>\n<li>Reports created before modification will have previous SLA targets applied. </li>\n</ul>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/docs.hackerone.com/static/response-sla-b95d8e77678cf7ba0f35eacd785b59ec-4e84c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 59.02140672782874%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"response sla\"\n        title=\"\"\n        src=\"/docs.hackerone.com/static/response-sla-b95d8e77678cf7ba0f35eacd785b59ec-e7ffa.png\"\n        srcset=\"/docs.hackerone.com/static/response-sla-b95d8e77678cf7ba0f35eacd785b59ec-49813.png 125w,\n/docs.hackerone.com/static/response-sla-b95d8e77678cf7ba0f35eacd785b59ec-dc8a5.png 250w,\n/docs.hackerone.com/static/response-sla-b95d8e77678cf7ba0f35eacd785b59ec-e7ffa.png 500w,\n/docs.hackerone.com/static/response-sla-b95d8e77678cf7ba0f35eacd785b59ec-dda24.png 750w,\n/docs.hackerone.com/static/response-sla-b95d8e77678cf7ba0f35eacd785b59ec-4e84c.png 981w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/programs/setting-response-slas","title":"Setting Response SLAs"}}},"pathContext":{}}

